import uniFav from '@/components/uni-fav/uni-fav.vue'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
export default {
	components: {
		uniFav,
		uniNavBar
	},
	data() {
		return {
			checkList: [false, false, false, false, false, false],
			contentText: {
				contentDefault: '追番',
				contentFav: '已追番'
			}
		}
	},
	methods: {
		favClick(index) {
			this.checkList[index] = !this.checkList[index]
			this.$forceUpdate()
		}
	}
}
